* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

input[type="checkbox"] {
  margin-right: 10px;
}

.container {
  width: 80%;
  margin: 15px auto;
}

.btn-sbmt {
  border: 0;
  background: #555;
  color: #fff;
  cursor: pointer;
}

.btn-sbmt:hover {
  background: #666;
}